nav {
	display: flex;
	justify-content: space-between;
	padding-inline: 2rem;
	height: 9rem;
	position: relative;
}

#nav-left {
	position: relative;
	align-self: center;
}

#logo {
	cursor: pointer;
	transition: 0.25s ease;
}

#logo:hover {
	padding-left: 1em;
}

#nav-right {
	display: flex;
	z-index: 1000;
	gap: 0.5em;
}

.lang-switcher {
	align-self: center;
	margin-bottom: 1em;
	margin-right: 1em;
	position: relative;
}

.lang-switcher>.lang-button {
	width: 2.5em;
	height: 2.5em;
	border-radius: 50%;
	cursor: pointer;
}

.lang-switcher>.lang-button img {
	position: absolute;
	border-radius: inherit;
	border: 1px solid var(--primary-700);
	width: 100%;
	height: 100%;
	inset: 0;
	transition: 0.125s ease;
}

.lang-button:not(.disabled):hover>img {
	filter: brightness(1.5)
}

.lang-button:not(.disabled):active>img {
	filter: brightness(0.75)
}

#lang-list {
	position: absolute;
	top: 3em;
	left: -4.5em;
	right: -1.5em;
	display: flex;
	flex-wrap: wrap;
	gap: 0.25em;
	justify-content: center;
	overflow: hidden;
}

@keyframes stuffs {
	from {
		transform: scale(0);
	}

	to {
		transform: scale(1);
	}
}

#lang-list .lang-button {
	background: none;
	border: 1px solid var(--gray-700);
	border-radius: 50%;
	width: 2em;
	height: 2em;
	transform: scale(0);
	position: relative;
	cursor: pointer;

	animation: stuffs 0.125s ease;
	animation-fill-mode: forwards;
}

#lang-list .lang-button img {
	position: absolute;
	border-radius: inherit;
	width: 100%;
	height: 100%;
	inset: 0;
	transition: 0.125s ease;
}

.disabled {
	cursor: default !important;
	filter: brightness(0.5)
}

.nav-item {
	display: flex;
	position: relative;
	width: 60px;
	height: 6rem;
	margin-top: -1px;
	align-items: center;
	justify-content: center;
	transition: 0.25s ease;
	border: 1px solid transparent;
	border-bottom-left-radius: 30px;
	border-bottom-right-radius: 30px;
	background: var(--gray-100);
	background-clip: padding-box;
	user-select: none;
	scroll-behavior: smooth;
}

.nav-item::after {
	content: "";
	z-index: -1;
	position: absolute;
	top: -1px;
	bottom: -1px;
	left: -1px;
	right: -1px;
	background: var(--primary-500);
	border-bottom-left-radius: 30px;
	border-bottom-right-radius: 30px;
	box-shadow: 0 0 0.5em rgba(0, 0, 0, 0.5);
}

.nav-item svg {
	fill: var(--gray-900);
}

.nav-item:hover {
	height: 8rem;
	cursor: pointer;
}

.nav-item:hover::after {
	background: linear-gradient(45deg, var(--primary-500), var(--secondary));
	box-shadow: 0 0 0.25em var(--primary-500);
}

.nav-item:active {
	background: var(--gray-300);
	box-shadow: 0 0 0.5em var(--primary-500);
}

.nav-item:active::after {
	box-shadow: 0 0 0.5em var(--primary-500);
}

.menu-category-title {
	background: url("./assets/title-bg.png") no-repeat;
	filter: drop-shadow(0 0 0.1em var(--primary-500));
	text-shadow: 0 0 0.25em var(--gray-100);
	width: 400px;
	height: 48px;
	transition: 0.25s ease;
	user-select: none;
	padding-left: 24px;
	line-height: 48px;
	scroll-margin-top: 1em;
	margin-bottom: 0.5em;
}

.menu-category-nav {
	margin-bottom: 1em;
}

.menu-category-nav button {
	display: inline-block;
	margin: 0.25em;
	background: transparent;
	border: 1px solid transparent;
	border-bottom: 1px solid var(--primary-500);
	font-size: 11px;
	font-family: "Mazzard H Medium";
	color: var(--gray-900);
	text-decoration: none;
	list-style: none;
	transition: 0.25s ease;
	cursor: pointer;

	font-size: 13px;
	color: var(--gray-900);
	text-decoration: none;
	padding-inline: 1em;
	padding-block: 0.5em;
	box-sizing: border-box;
}

.menu-category-nav button:hover {
	border-radius: 0.5em;
	background-color: var(--primary-300);
	border: 1px solid var(--primary-500);
}

.menu-group {
	scroll-margin-top: 1.25em;
	clear: both;
	margin-bottom: 1.5em;
}

.menu-group-title {
	text-align: center;
	margin-bottom: 1em;
}

.menu-group-info {
	display: flex;
	background: url("./assets/allergen/info.svg") 1em center no-repeat;
	background-size: 1.25em, cover;
	align-items: center;
	padding: 1em 3em;
	border-bottom: 1px solid var(--primary-500);
	margin-top: -1em;
	margin-bottom: 1em;
	font-size: 13px;
}

main h1:not(:first-child) {
	margin-top: 2em;
}

article {
	padding: 1em;
	background: radial-gradient(var(--gray-500), var(--gray-100));
	border: 1px solid var(--gray-500);
	border-radius: 1em;
	transition: 0.25s ease;
	cursor: default;
}

article:hover {
	filter: drop-shadow(0 0 0.5em var(--primary-500));
	border-color: var(--primary-500);
}

article:hover>.image-container {
	height: 140px;
}

article .new-item {
	border-color: var(--label-new-border)
}

article .popular-item {
	border-color: var(--label-popular-border)
}

article .discount-item {
	border-color: var(--label-discount-border)
}

.item-info {
	display: flex;
	justify-content: space-between;
	padding-inline: 0.5em;
	margin-top: 0.5em;
	color: var(--gray-900);
}

.item-info span {
	display: flex;
	font-size: 0.9em;
	align-items: center;
	gap: 0.25em;
	margin-left: 0.25em;
}

.item-subtitle {
	font-size: 13px;
}

.item-description {
	color: var(--gray-700);
	font-size: 12px;
	margin-top: 0.25em;
	margin-bottom: 0;
}

.image-container {
	width: 100%;
	height: 128px;
	overflow: hidden;
	border: 1px solid var(--gray-500);
	border-radius: 0.5em;
	transition: height 0.25s ease;
	cursor: pointer;
	margin-block: 0.5em;
}

.image-container:active {
	filter: brightness(0.75);
}

.image-container img {
	width: 100%;
	height: 100%;
	aspect-ratio: 16 / 9;
	object-fit: cover;
}

.menu-tag {
	background-color: var(--gray-100);
	border: 1px solid var(--gray-500);
	border-radius: 0.75em;
	font-size: 11px;
	font-family: "Mazzard H Medium";
	color: var(--gray-700);
	padding: 3px 12px;
	user-select: none;
	cursor: pointer;
	transition: 0.25s ease;
}

.menu-tag:hover {
	background-color: var(--primary-500);
	border-color: var(--primary-700);
	color: #fff;
}

/* Discount */
.tag-0 {
	background-color: var(--label-discount);
	border-color: var(--label-discount-border);
	color: var(--gray-100);
}

/* Popular */
.tag-1 {
	background-color: var(--label-popular);
	border-color: var(--label-popular-border);
	color: var(--gray-100);
}

/* New */
.tag-2 {
	background-color: var(--label-new);
	border-color: var(--label-new-border);
	color: var(--gray-100);
}

.menu-item-allergen {
	display: flex;
	position: relative;
	align-items: center;
	margin-top: 0.25em;
}

.menu-item-allergen:hover>.allergen-information,
.allergen-information:hover>.allergen-information {
	display: grid;
}

.menu-item-allergen svg {
	width: 1em;
	height: 1em;
	padding: 0;
}

.menu-item-allergen svg:not(:last-child) {
	padding-right: 0.25em;
}

#tooltip {
	position: absolute;
	top: 0;
	left: 0;
	padding: 1em 1.25em;
	font-size: 12px;
	user-select: none;
	z-index: 1;

	display: grid;
	align-items: center;
	grid-template-columns: repeat(2, auto);
	text-transform: uppercase;
	gap: 0.25em 0.5em;
	filter: drop-shadow(0 0 0.25em black);
}

#tooltip::before {
	content: "";
	position: absolute;
	top: -0.25em;
	left: 1em;
	width: 1em;
	height: 1em;
	background: var(--gray-700);
	transform: rotate(45deg);
	z-index: -1;
}

#tooltip::after {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: var(--gray-300);
	border: 1px solid var(--gray-700);
	border-radius: 0.75em;
	z-index: -1;
}

#scroll-to-top {
	position: fixed;
	right: 2em;
	bottom: 2em;
	aspect-ratio: 1;
	background: var(--gray-100);
	border: 1px solid var(--primary-500);
	border-radius: 50%;
	cursor: pointer;
	transition: 0.25s ease;
}

#scroll-to-top svg {
	width: 2.5em;
	height: 2.5em;
	fill: var(--gray-900);
}

#scroll-to-top:hover {
	background: var(--gray-300);
}

.hidden {
	opacity: 0;
	user-select: none;
	pointer-events: none;
}

@media only screen and (max-width: 1200px) {
	.menu-items {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media only screen and (max-width: 1024px) {
	body {
		padding: 0 5%;
		padding-bottom: 4.5em;
	}

	#scroll-to-top svg {
		margin: 0.5em;
	}
}

@media only screen and (max-width: 800px) {
	nav {
		flex-direction: column;
		justify-content: center;
		height: auto;
		padding-inline: unset;
		padding-block: 1em;
		align-items: center;
		margin-bottom: 1em;
	}

	#nav-left {
		margin-block: 1em;
	}

	#nav-right {
		width: 100%;
	}

	.lang-switcher {
		position: absolute;
		margin: 0;
		top: 2em;
		right: 0.5em;
	}

	.lang-switcher button {
		width: 2em;
		height: 2em;
	}

	#lang-list {
		position: absolute;
		top: 3em;
		padding: 0.5em;
		background: var(--gray-300);
		border: 1px solid var(--primary-700);
		border-radius: 2em;
		left: auto;
		right: -0.63em;
		display: flex;
		flex-direction: column;
		flex-wrap: nowrap;
		gap: 0.5em;
		z-index: 999;
	}

	#lang-list .lang-button {
		background: none;
		border: 1px solid var(--primary-700);
		border-radius: 50%;
		width: 2.5em;
		height: 2.5em;
		position: relative;
		cursor: pointer;
	}

	#lang-list .lang-button img {
		position: absolute;
		border-radius: inherit;
		width: 100%;
		height: 100%;
		inset: 0;
		transition: 0.125s ease;
	}

	.nav-item {
		flex: 1;
		width: auto;
		height: 2.5em;
		border-radius: 1em;
	}

	.nav-item:hover {
		height: auto;
	}

	.nav-item::after {
		border-radius: inherit;
	}

	.nav-item::before {
		content: attr(data-title);
		position: absolute;
		bottom: -3em;
		height: 2em;
		color: var(--gray-700);
		font-size: 10px;
		font-family: "Mazzard H";
	}

	.menu-category-title {
		background: none;
		filter: none;
		width: 80%;
		height: auto;
		margin: auto;
		margin-bottom: 0.5em;
		text-align: center;
		padding: 0;
		border-bottom: 1px solid var(--primary-500);
	}

	.menu-category-nav button {
		background-color: var(--primary-300);
		border: 1px solid var(--primary-500);
		border-radius: 0.5em;
	}

	.menu-category-nav button:hover {
		background: var(--primary-500);
	}

	.menu-items {
		grid-template-columns: repeat(1, 1fr) !important;
	}
}